.order{
  margin-top: 2.5em;
  @include grid100gap(1.75em);
}

.order-data{
  border-radius: 1.5em;
  border: $border;
  padding: 2.5em;
  &__title{
    font-size: 1.75em;
  }
}

.order-data{
  &__fields{
    display: grid;
    grid-template-columns: repeat(2, calc(50% - .75em));
    gap: 1.5em;
    margin-top: 1.5em;
    &._col3{
      @media screen and (min-width: 993px){
        grid-template-columns: repeat(3, calc(100% / 3 - 1em));
      }
    }
  }
  &__nav{
    display: flex;
    margin-top: 1.5em;
  }
}

.delivery-ways{
  display: grid;
  grid-template-columns: repeat(4, calc(25% - 3.75em / 4));
  gap: 1.25em;
  margin-top: 1.5em;
}

.delivery-way{
  input:checked~.delivery-way__inner{
    border-color: $primary;
  }
  &__inner{
    transition: all .3s;
    min-height: 100%;
    height: 14.5em;
    padding: 2em;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 1.25em;
    background: $white;
    border: 1px solid $white;
    box-shadow: 0px 0px 16.2px 1.8px rgba(0, 0, 0, 0.11);
  }
  &__img{
    height: 1.75em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__price{
    font-size: 1.5em;
    font-weight: 700;
    line-height: 140%;
  }
}

.pay-items{
  margin-top: .5em;
  display: flex;
  flex-wrap: wrap;
}

.pay-item{
  @include center;
  width: 4.5em;
  margin: 1em 1.2em 0 0;
  img{
    width: 100%;
  }
}

.delivery-ways2{
  margin-top: 1.5em;
  display: flex;
}

.delivery-way2{
  padding-left: 2em;
  margin-right: 3em;
  &__radio{
    content: '';
    position: absolute;
    width: 1.25em;
    height: 1.25em;
    left: 0;
    top: .125em;
    border: .25em solid $line;
    background-color: $line;
    border-radius: 50%;
  }
  &__price{
    margin-top: .5em;
  }
  input:checked~.delivery-way2__radio{
    border-color: #6A4ED4;
    background-color: transparent;
  }
}


@media screen and (max-width: 992px){
  .order-data{
    border-radius: 1em;
    padding: 1.25em;
    &__title{
      font-size: 1.5em;
    }
  }

  .delivery-ways{
    grid-template-columns: repeat(4, calc(25% - 2.25em / 4));
    gap: .75em;
  }

  .delivery-way{
    font-size: .75em;
    &__inner{
      height: 12em;
    }
  }
}


@media screen and (max-width: 576px){
  .order-data{
    &__fields{
      grid-template-columns: 100%;
      gap: 1em;
      margin-top: 1em;
    }
  }

  .delivery-ways{
    grid-template-columns: repeat(2, calc(50% - .25em));
    gap: .5em;
  }

  .delivery-ways2{
    @include grid100gap(1em);
  }

  .delivery-way2{
    margin: 0;
    &__desc{
      margin-top: .25em;
    }
  }
}