.page-top{
    &__nav{
        margin-top: 1.5em;
    }
    &__back{
        display: flex;
        align-items: center;
        svg{
            width: .75em;
            margin-right: .625em;
        }
    }
}

.breadcrumbs {
    margin-bottom: 1.5em;
    li{
        position: relative;
        display: inline;
        color: $gray;
        &:not(:first-child) {
            margin-left: 2em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                top: .375em;
                right: 100%;
                margin-right: .8125em;
                background-image: url(../img/bread-arrow.svg);
                @include bgImgSize(100%);
                width: .75em;
                height: .75em;
            }
        }
        &:last-child{
            pointer-events: none;
        }
    }
    a{
        font-size: .875em;
        line-height: 140%;
    }
}