.pagination{
  ul{
    @include center;
  }
  li{
    margin: 0 .25em;
  }
  a{
    @include center;
    min-width: 2.5em;
    height: 2.5em;
    padding: 0 .5em;
    border-radius: .375em;
  }
  .active{
    a{
      background-color: $primary;
      color: $white;
    }
  }
  .prev,
  .next{
    a{
      padding: 0 .875em;
    }
  }
  .disabled{
    color: $gray;
    pointer-events: none;
  }
}