.lk{
  &__tabs{
    display: flex;
  }
  &__tab{
    padding: 0 2em;
    height: 3.125em;
    display: flex;
    align-items: center;
    border-radius: 0.625em;
    background: $white;
    border: 2px solid $white;
    box-shadow: 0 0 1em .125em rgba(0, 0, 0, 0.11);
    font-weight: 700;
    margin-right: 1.5em;
    &:last-child{
      margin-right: 0;
    }
    span{
      font-size: 1.125em;
    }
    &:hover{
      color: $red;
    }
    &._active{
      border-color: $red;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 35.9375em - 1.875em) 35.9375em;
    gap: 1.875em;
    margin-top: 3.25em;
  }
  &__bottom{
    margin-top: 2.5em;
  }
  &__logout{
    height: 3.75em;
    padding: 0 2.66em;
    span{
      font-size: 1.3125em;
    }
  }
}

.user-form{
  padding: 1.6em;
  border-radius: 1.25em;
  box-shadow: 0px 0px 16.2px 1.8px rgba(0, 0, 0, 0.11);
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1em 1.875em;
  }
  .fg{
    label{
      margin-bottom: .78em;
      font-size: 1.125em;
      line-height: 143%;
      font-weight: 700;
      span{
        color: $primary;
      }
    }
    input{
      border-radius: .625em;
      font-size: 1em;
      height: 2.625em;
      padding: 0 1em;
      background-color: $white;
      border-color: $white;
      box-shadow: 0px 0px 16.2px 1.8px rgba(0, 0, 0, 0.11);
      &:focus {
        background-color: #F5F6F8;
        border-color: $red;
      }
    }
  }
  .m-check{
    margin-top: 1.5em;
    label{
      padding-left: 2.81em;
      padding-top: .1875em;
      span{
        font-size: .9375em;
        line-height: 143%;
      }
      &:before{
        box-shadow: 0px 0px 16.2px 1.8px rgba(0, 0, 0, 0.11);
        width: 1.75em;
        height: 1.75em;
        border-radius: 0.375em;
        background-size: 0 auto;
      }
    }
    input:checked~label{
      &:before{
        background-color: $primary;
      }
    }
  }
  &__btn{
    margin-top: 1.6em;
    padding: 0 2em;
  }
  &--password{
    .fg{
      grid-column: 1/2;
    }
  }
}

.favourite{
  margin-top: 5em;
  &__items{
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 2em));
    gap: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .lk{
    overflow: hidden;
    &__tabs{
      @include fullScroll;
      padding: 1em;
    }
    &__tab{
      margin-right: 1em;
      white-space: nowrap;
      padding: 0 1.5em;
      height: 2.75em;
      span{
        font-size: 1em;
      }
    }
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1em;
    }
  }

  .favourite{
    margin-top: 2em;
    &__items{
      grid-template-columns: repeat(3,calc(100% / 3 - 1em));
      gap: 2.5em 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .lk{
    &__bottom{
      margin-top: 1em;
    }
    &__logout{
      width: 100%;
    }
  }

  .user-form{
    border-radius: 1em;
    padding: 1em;
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .favourite{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .5em));
      column-gap: 1em;
    }
  }
}
