.cart{
  padding-top: 1.5em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 22.5em) 21em;
    gap: 1.5em;
  }
  &__items{
    margin-top: 1.5em;
  }
}

.cart-item{
  padding: 1.5em 0;
  border-bottom: $border;
  display: grid;
  grid-template-columns: auto 7.5em 6.5em 5em;
  align-items: center;
  gap: 2.5em;
  &:first-child{
    padding-top: 0;
  }
  &:last-child{
    padding-bottom: 0;
    border-bottom: none;
  }
  &__col{
    display: grid;
    grid-template-columns: 6em calc(100% - 8.5em);
    gap: 2.5em;
    align-items: center;
  }
  &__img{
    @include imgCover(100%);
    border-radius: .5em;
  }
  .product-card__prices{
    margin: 0;
    flex-wrap: wrap;
    gap: .75em;
  }
  .product-card__price2{
    margin: 0;
  }
  &__delete{
    width: 1.25em;
    @include center;
    margin-left: 2.5em;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        stroke: $red;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .cart{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .cart-item{
    gap: 1.25em;
    grid-template-columns: auto 7.5em 6.5em 3.75em;
    &__col{
      grid-template-columns: 5em calc(100% - 6.25em);
      gap: 1.25em;
    }
    &__delete{
      margin-left: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .cart-item{
    padding-right: 2em;
    position: relative;
    grid-template-columns: repeat(2,calc(50% - .625em));
    &:first-child{
      .cart-item__col4{
        top: 0;
      }
    }
    &__col{
      align-items: flex-start;
      grid-column: 1/-1;
      grid-template-columns: 4em calc(100% - 5em);
      gap: 1em;
    }
    &__col4{
      position: absolute;
      top: 1.5em;
      right: 0;
    }
    &__action{
      flex-direction: column;
    }
    &__delete{
      margin: 1em 0 0;
    }
  }
}
