.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: .9375em;
    line-height: 140%;
    margin-bottom: .375em;
    span{
      color: $primary;
    }
  }
  .nice-select,
  input,
  textarea{
    border-radius: 0.57em;
    border: 1px solid $bg;
    padding: 0 1.14em;
    height: 3.72em;
    width: 100%;
    font-size: .875em;
    background-color: $bg;
    color: $black;
    &::placeholder {
      color: $gray;
    }
    &:focus {
      border-color: $primary;
    }
    &:disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      color: rgba(#000,.4);
    }
  }
  textarea{
    padding-top: .75em;
    padding-bottom: .75em;
    height: 8.5em;
    resize: none;
  }
  &__field{
    position: relative;
  }
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: .625em;
    white-space: nowrap;
    height: 3.75em;
    padding: 0 1.25em;
    background-color: #EFEFEF !important;
    border: none;
    float: unset;
    font-size: 1em;
    &.open{
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
    &:before{
      content: '';
      order: 3;
      flex-shrink: 0;
      width: .625em;
      height: .625em;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(100%);
      margin-left: 1em;
    }
    .current{
      font-size: 1.0625em;
      line-height: 157%;
    }
    .list{
      margin: 0;
      padding: .25em 1.25em .75em;
      border-radius: 0 0 .625em .625em;
      border: none;
      background: #EFEFEF;
      box-shadow: none;
      max-height: 14.875em;
      overflow-y: auto;
      z-index: 5;
      @include scroll;
    }
    .option{
      height: unset;
      font-size: 1.0625em;
      line-height: 123%;
      padding: .625em 0;
      display: flex;
      align-items: center;
      color: $black;
      border: none;
      border-top: 1px solid #DDDDDD;
      &:last-child{
        border-bottom: none;
      }
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: transparent;
        font-weight: 400;
        color: $primary;
      }
    }
  }
  &._error{
    input,
    textarea{
      border-color: $primary;
      color: $primary;
      &::placeholder{
        color: $primary;
      }
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .375em;
    color: $primary;
    display: none;
  }
}

.pass-toggle{
  position: absolute;
  bottom: 1em;
  right: 1.25em;
  width: 1.25em;
  height: 1.6875em;
  @include center;
  svg{
    width: 100%;
    height: auto;
  }
  &._active{
    g{
      opacity: 1;
    }
  }
}

.delivery-way,
.delivery-way2,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 2.125em;
    min-height: 1.5em;
    padding-top: .125em;
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      width: 1.5em;
      height: 1.5em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
    span{
      font-size: .875em;
      line-height: 140%;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-size: 0 auto, 100% auto;
      }
    }
  }
  label {
    &:before {
      @include bgImgSize(100%);
      background-image: url(../img/check.svg),url(../img/check2.svg);
      background-size: 100% auto, 0 auto;
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
    span{
      display: block;
      line-height: 157%;
      color: #5B626C;
      margin-top: .125em;
    }
  }
}

.cartcalc{
  max-width: 100%;
  width: 7.5625em;
  display: grid;
  grid-template-columns: 1em calc(100% - 2em) 1em;
  background-color: $bg;
  border-radius: .5em;
  height: 2.8125em;
  padding: 0 1.25em;
  button,
  input{
    width: 100%;
  }
  input{
    border: none;
    color: $black;
    font-size: .875em;
    text-align: center;
    background-color: transparent;
  }
  svg{
    width: 100%;
    height: auto;
  }
  button{
    background-color: transparent;
    @include center;
  }
  ._disabled{
    //pointer-events: none;
    path{
      stroke: $gray;
    }
  }
}


