@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.5em;
    border-radius: .5em;
    height: 3em;
    font-weight: 700;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-gray-outline{
    border: $border;
    color: $black;
    &:hover{
        border-color: $primary;
        background-color: $primary;
        color: $white;
    }
}

.m-btn-gray{
    background-color: $bg;
    color: $black;
    &:hover{
        background-color: $primary;
        color: $white;
    }
}

.m-btn-white{
    background-color: $white;
    color: $black;
    box-shadow: 0px 0px 16.2px 1.8px rgba(0, 0, 0, 0.11);
    &:hover{
        background-color: $primary;
        color: $white;
    }
}

.m-btn-blue-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        background-color: $primary;
        color: $white;
    }
}

.m-btn-blue{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: darken($primary, 10%);
        color: $white;
    }
}

.m-btn-red{
    background-color: $red;
    color: $white;
    &:hover{
        background-color: darken($red, 10%);
        color: $white;
    }
}