.orders{
  &__items{
    @include grid100gap(1em);
  }
}

.order-group{
  border-radius: 1.25em;
  box-shadow: 0px 0px 16.2px 1.8px rgba(0, 0, 0, 0.11);
  background-color: $white;
  &._active{
    .order-group__toggle{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
      svg{
        transform: rotate(-180deg);
      }
    }
  }
  &__show{
    padding: 1.25em 1.875em;
    cursor: pointer;
  }
  &__title{
    font-size: 1.125em;
    line-height: 143%;
    font-weight: 700;
  }
  &__date{
    font-size: .875em;
    line-height: 114%;
    color: #777;
    margin-left: .75em;
    padding-right: 1em;
  }
  &__toggle{
    display: flex;
    align-items: center;
    margin-left: auto;
    svg{
      width: .75em;
      margin-left: .625em;
    }
    span{
      font-size: .875em;
    }
    ._view2{
      display: none;
    }
    &:hover{
      color: $red;
      path{
        fill: $red;
      }
    }
  }
  &__hidden{
    padding: 1.875em 1.875em 4.44em;
    display: none;
  }
  &__items{
    @include grid100gap(2.5em);
  }
  &__bottom{
    margin-top: 3.375em;
  }
  &__btn{
    padding: 0 2.5em;
    height: 3.125em;
    border-radius: .625em;
    margin-right: 1.25em;
    &:last-child{
      margin: 0;
    }
  }
  &__summary{
    &-title{
      font-size: 1.125em;
      line-height: 143%;
      font-weight: 700;
      margin-right: 1.1em;
    }
    &-title2{
      font-size: 1em;
      line-height: 140%;
      margin-right: 2.19em;
    }
    &-status{
      color: #65B32E;
    }
  }
}

.order-item{
  display: grid;
  grid-template-columns: 6em 22em auto;
  gap: .625em;
  align-items: flex-start;
  &__col2{
    padding-top: .81em;
  }
  &__col3{
    padding-top: .44em;
  }
  &__img{
    @include imgCover(100%);
    border-radius: .5em;
  }
  &__articul{
    font-weight: 700;
    font-size: .75em;
    line-height: 160%;
    margin-top: .25em;
  }
  .product-card__prices{
    justify-content: flex-end;
    margin: 0;
  }
  .product-card__price2{
    margin: 0 .75em 0 0;
  }
  &__count{
    text-align: right;
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .order-group{
    border-radius: 1em;
    &__show{
      padding: .75em 1em;
    }
    &__hidden{
      padding: .75em 1em 1em;
    }
    &__items{
      gap: 1.5em;
    }
    &__info{
      display: block;
    }
    &__date{
      margin: .5em 0 0;
    }
    &__bottom{
      margin-top: 2em;
      display: block;
    }
    &__nav{
      display: block;
      margin-top: 1em;
    }
    &__btn{
      margin: 0 0 .5em;
      width: 100%;
    }
  }

  .order-item{
    grid-template-columns: 4em calc(100% - 4.625em);
    &__col2{
      padding: 0;
    }
    &__col3{
      padding: 0;
      grid-column: 1/-1;
    }
    .product-card__prices{
      justify-content: unset;
    }
    &__count{
      text-align: left;
    }
  }
}
