.login{
  .user-form{
    max-width: 52em;
    width: 100%;
    margin: 0 auto;
  }
}

.login-form{
  width: 26.25em;
  max-width: 100%;
  margin: 0 auto;
  &__btn{
    width: 100%;
    margin-top: 1.25em;
  }
  &__bottom{
    margin-top: 1.94em;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__link{
    font-size: 1em;
    line-height: 142%;
    color: #898989;
    font-weight: 600;
    border-bottom: 1px solid #898989;
    margin-bottom: 1.375em;
    &:last-child{
      margin-bottom: 0;
    }
    &--dark{
      font-weight: 700;
      color: #4D4D4D;
      border-color: #4D4D4D;
    }
  }
}