.header-menu{
  position: absolute;
  left: 8.375em;
  top: 100%;
  margin-top: .625em;
  padding: 1.25em;
  background-color: $white;
  box-shadow: $shadow;
  width: 50em;
  min-height: 28.5em;
  border-radius: .5em;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &__items{
    position: relative;
    width: 18em;
    @include grid100gap(.25em);
  }
  hr{
    margin: .75em 0;
    background-color: $line;
    color: $line;
    border: none;
    height: 1px;
  }
}

.m-link{
  display: flex;
  align-items: center;
  padding: 0 1em;
  border-radius: 0.375em;
  height: 3.25em;
  transition: all .3s;
  &._active,
  &:hover{
    cursor: pointer;
    background-color: $bg;
    color: $primary;
  }
  img{
    width: 1.125em;
    margin-right: .5em;
  }
  &--bg{
    background-color: $bg;
  }
}

.menu-item--has-children .m-link,
.m-link--arrow{
  &:after{
    content: '';
    margin-left: auto;
    background-image: url(../img/arrow-right.svg);
    @include bgImgSize(100%);
    width: .75em;
    height: .75em;
    flex-shrink: 0;
    transition: all .3s;
  }
}

.menu-item{
  &__hidden{
    ul{
      @include grid100gap(1.25em);
    }
    a{
      font-size: 1em;
      line-height: 150%;
    }
  }
  @media screen and (min-width: 993px){
    &:hover{
      .menu-item__show{
        background-color: $bg;
        color: $primary;
      }
      .menu-item__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__hidden{
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      position: absolute;
      top: 0;
      left: 100%;
      margin-left: -1px;
      padding-left: 2.5em;
      width: 29.5em;
      min-height: 100%;
      display: block !important;
    }
  }
}


@media screen and (max-width: 992px){
  .header-menu{
    position: fixed;
    top: 0;
    left: 0;
    margin: 0;
    width: 100%;
    //opacity: 1;
    //pointer-events: unset;
    height: $vh;
    padding: 5em 0 0;
    z-index: 5;
    &__scroll{
      overflow-y: auto;
      padding: 0 1em 1em;
      height: 100%;
    }
    &__items{
      width: 100%;
    }
  }

  .menu-item{
    &._active{
      .menu-item__show:after{
        transform: rotate(90deg);
      }
    }
    &__show{
      background-color: $bg;
    }
    &__hidden{
      padding: .75em 0 .75em 1em;
      display: none;
      ul{
        font-size: .875em;
        gap: 1em;
      }
    }
  }
}


