.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .43;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 36.5em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 5.75em 4.25em 3.75em;
        border-radius: 1.25em;
        display: inline-block;
    }
    &__title{
        font-size: 2.25em;
        color: #3B3B3B;
        font-weight: 700;
        text-align: center;
    }
    .close-btn{
        position: absolute;
        top: 1.25em;
        right: 1.25em;
    }
    .login-form{
        margin-top: 2em;
    }
}

.close-btn{
    width: 2.875em;
    @include center;
    flex-shrink: 0;
    img,
    svg{
        width: 100%;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {
            padding: 3em 1em 1em;
        }
        &__title{
            font-size: 1.5em;
        }
        .close-btn{
            width: 1.5em;
            top: 1em;
            right: 1em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
    }
}
