.header{
  position: relative;
  z-index: 5;
  padding-bottom: 1.5em;
  &__inner{
    display: flex;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 6.875em;
    margin-right: 1.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__catalog-btn{
    height: 3.25em;
    margin-right: 1em;
    img{
      width: 1.5em;
      margin-right: .75em;
    }
    ._view2{
      display: none;
    }
  }
  &._open{
    .header-menu{
      opacity: 1;
      pointer-events: unset;
    }
    .header__catalog-btn{
      pointer-events: none;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
  @media screen and (min-width: 993px){
    &._fix{
      .header-middle{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        padding: 1em 0;
        z-index: 10;
        box-shadow: $shadow;
      }
    }
  }
}

.header-top{
  border-bottom: $border;
  padding: 1em 0;
}

.nav{
  font-weight: 600;
  color: $gray;
  ul{
    display: flex;
  }
  li{
    margin-right: 1.75em;
    &:last-child{
      margin: 0;
    }
  }
  a{
    font-size: .875em;
    line-height: 130%;
  }
}

.header-contacts{
  &__phone{
    margin-right: 2em;
    svg{
      width: 1.125em;
      margin-right: .375em;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
}

.burger{
  display: none;
}

.header-middle{
  background-color: $white;
  padding: 1.5em 0;
  &__inner{
    position: relative;
  }
}

.header-search{
  margin-right: 1.5em;
  flex-grow: 1;
  position: relative;
  &__btn{
    position: absolute;
    right: 1em;
    top: 50%;
    transform: translateY(-50%);
    width: 1.25em;
    @include center;
    img{
      width: 100%;
    }
  }
}

.header-action{
  &__link{
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $gray;
    margin-right: 1.5em;
    position: relative;
    &:last-child{
      margin: 0;
    }
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
    svg{
      height: 1.5em;
      width: auto;
      margin-bottom: .125em;
    }
    &-count{
      position: absolute;
      border-radius: .625em;
      height: 1.25em;
      min-width: 1.25em;
      padding: 0 .25em;
      @include center;
      background-color: $red;
      color: $white;
      font-weight: 600;
      right: .5em;
      top: -.375em;
    }
  }
}

.header-cart{
  &__count{
    opacity: 0;
  }
  &._active{
    .header-cart__count{
      opacity: 1;
    }
  }
}

.header-bottom{
  &__link{
    margin-right: .75em;
    height: 2.8125em;
    padding: 0 1.25em;
    span{
      font-size: .875em;
    }
    svg,
    img{
      width: 1.125em;
      margin-right: .5em;
    }
    &.m-btn-red{
      border: 2px solid $red;
      &:hover{
        background-color: transparent;
        color: $red;
        path{
          stroke: $red;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header-bottom{
    display: none;
  }

  .header{
    padding-bottom: 1em;
    &__logo{
      width: 6em;
      margin-right: auto;
      position: relative;
      z-index: 6;
    }
    &__catalog-btn{
      position: relative;
      z-index: 6;
      order: 2;
      width: 3em;
      height: 3em;
      padding: 0;
      margin: 0 0 0 1.5em;
      img{
        margin: 0;
      }
      span{
        display: none;
      }
    }
    &._open{
      .header__catalog-btn{
        pointer-events: unset;
      }
    }
  }

  .header-middle{
    padding: 1em 0;
    &__inner{
      flex-wrap: wrap;
      justify-content: flex-end;
    }
  }

  .header-action{
    &__link{
      margin-right: 1em;
      svg{
        margin: 0;
      }
      &-title{
        display: none;
      }
      &-count{
        right: -.5em;
      }
    }
  }

  .header-search{
    flex-grow: 1;
    width: 100%;
    margin: 1em 0 0;
    order: 3;
  }

  .header-top{
    padding: 0;
    margin-top: 1em;
    border: none;
    .container{
      padding: 0;
    }
    &__inner{
      display: block;
    }
  }

  .nav{
    ul{
      display: block;
    }
    li{
      margin: 0 0 .75em;
    }
  }

  .header-contacts{
    flex-direction: column;
    align-items: flex-start;
    margin-top: 2em;
    &__phone{
      margin: 0 0 .75em;
    }
  }
}


