.tabs{
  display: flex;
}

.tab{
  padding: 0 0 1.25em;
  display: flex;
  align-items: center;
  position: relative;
  font-weight: 700;
  transition: all .3s;
  @include lastM(0 2.5em 0 0);
  &:before{
    content: '';
    position: absolute;
    width: 100%;
    height: 3px;
    border-radius: .25em;
    left: 0;
    bottom: -1px;
    background-color: $primary;
    opacity: 0;
    transition: inherit;
  }
  span{
    font-size: 1em;
    line-height: 150%;
  }
  &:hover{
    color: $primary;
    cursor: pointer;
  }
  &._active{
    color: $primary;
    pointer-events: none;
    &:before{
      opacity: 1;
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

@media screen and (max-width: 576px){
  .tab{
    @include lastM(0 1.25em 0 0);
    span{
      font-size: .875em;
    }
  }
}