.blog{
  padding-top: 1.5em;
}

.blog-items{
  row-gap: 1.5em;
}

.blog-card{
  display: flex;
  flex-direction: column;
  &__img{
    border-radius: 1.5em;
    @include imgCover(56.35%);
  }
  &__content{
    margin-top: 1.25em;
    @include grid100gap(.75em);
    margin-bottom: .75em;
  }
  &__date{
    margin-top: auto;
  }
}

.blog-item{
  padding-top: 1.5em;
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 1.5em;
    }
  }
  &__grid{
    margin-top: 3em;
    display: grid;
    grid-template-columns: calc(50% - 23.75em) 43.5em calc(50% - 23.75em);
    gap: 1.5em;
  }
}

.blog-slider{
  .swiper-slide{
    width: calc(100% / 3);
  }
}


@media screen and (max-width: 992px){
  .blog-card{
    &__img{
      border-radius: 1em;
    }
    &__content{
      margin-top: .75em;
      gap: .5em;
      margin-bottom: .5em;
    }
  }

  .blog-item{
    &__img{
      img{
        border-radius: 1em;
      }
    }
    &__grid{
      margin-top: 1em;
      grid-template-columns: 100%;
      gap: .75em;
    }
  }
  
  .blog-collection{
    overflow: hidden;
  }

  .blog-slider{
    .slider__wrap{
      overflow: unset;
    }
    .swiper-slide{
      width: 48%;
    }
    .swiper-button{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .blog-slider{
    .swiper-slide{
      width: 93%;
    }
  }
}
