@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
@import "components/_filter-slider.scss";
.m-section{
  padding: 2em 0;
  &._first{
    padding-top: 0;
  }
}

.m-section2{
  padding: 2.94em 0;
}

._flex,
._flex-between,
._flex-center{
  display: flex;
  align-items: center;
  &._start{
    align-items: unset;
  }
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

._mt48{
  margin-top: 3em;
}

._mt40{
  margin-top: 2.5em;
}

._mt32{
  margin-top: 2em;
}

._mt16{
  margin-top: 1em;
}

._mt24{
  margin-top: 1.5em;
}

._mt4{
  margin-top: .25em;
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.12);
    pointer-events: none;
    border-radius: inherit;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2em;
    height: 2em;
    @include lastM(0 1.25em 0 0);
    @include center;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.favourite-btn{
  @include center;
  width: 1.25em;
  svg{
    width: 100%;
    height: auto;
  }
  &:hover{
    path{
      stroke: $red;
    }
  }
  &._added{
    path{
      opacity: 1;
      stroke: $red;
      fill: $red;
    }
  }
}

.qa-card{
  padding: 1.25em 2.5em;
  border-radius: 1.25em;
  background: $white;
  &._active{
    .qa-card__btn img{
      transform: rotate(-45deg);
    }
  }
  &__show{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 6.25em;
  }
  &__btn{
    margin-left: 1em;
    width: 2.5em;
    @include center;
    flex-shrink: 0;
    img{
      transition: all .3s;
      width: 100%;
    }
  }
  &__hidden{
    margin-top: -.625em;
    padding-top: 1.25em;
    display: none;
    border-top: 1px solid #E3E3E3;
  }
}


@media screen and (max-width: 992px){
  .m-section,
  .m-section2{
    padding: 1.25em 0;
  }

  ._mt16-mob{
    margin-top: 1em;
  }
}


