.profi,
.subscribe{
  &__inner{
    position: relative;
    padding: 3em 4em;
    border-radius: 1.5em;
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    border-radius: inherit;
    img{
      width: 100%;
      height: 100%;
      display: block;
      border-radius: inherit;
      object-fit: cover;
    }
  }
}

.profi{
  &__content{
    max-width: 39.5em;
  }
  &__subtitle{
    margin-top: .75em;
  }
  &__desc{
    margin-top: .375em;
  }
}

.subscribe{
  &__desc{
    margin-top: .625em;
  }
}

.subscribe-form{
  margin-top: 1.5em;
  width: 100%;
  max-width: 39.5em;
  &__grid{
    display: flex;
  }
  .fg{
    flex-grow: 1;
    input{
      background-color: $white;
      border-color: $white;
      &:focus {
        border-color: $primary;
      }
    }
  }
  &__btn{
    height: 3.25em;
    margin-left: 1em;
    flex-shrink: 0;
  }
}

.main-banner{
  display: block;
  img{
    width: 100%;
    //min-height: 23.75em;
    object-fit: cover;
    display: block;
    border-radius: 1.5em;
  }
}

.main-item{
  text-align: center;
  display: block;
  &__img{
    @include imgCover(65%);
    margin-bottom: 1.5em;
  }
}


@media screen and (max-width: 992px){
  .profi,
  .subscribe{
    &__inner{
      border-radius: 1em;
      padding: 2em 1em;
      display: block;
    }
  }

  .profi{
    &__nav{
      display: flex;
      margin-top: 1em;
    }
  }

  .subscribe-form{
    max-width: 100%;
  }

  .main-banner{
    img{
      border-radius: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .subscribe-form{
    &__grid{
      @include grid100gap(.75em);
    }
    &__btn{
      margin: 0;
    }
  }
}
