.catalog-item{
  padding-top: 1.5em;
  &__code{
    text-align: right;
  }
  &__grid{
    display: grid;
    grid-template-columns: 36em calc(100% - 60em) 21em;
    gap: 1.5em;
  }
  &__reviews{
    .rating-block{
      margin-right: .75em;
    }
  }
  &__info{
    @include grid100gap(.375em);
  }
}

.gallery{
  display: grid;
  grid-template-columns: calc(100% - 31.25em) 30em;
  gap: 1.25em;
  &__small,
  &__big{
    height: 30em;
  }
  .swiper{
    height: 100%;
  }
  &__img{
    @include imgCover(100%);
    border-radius: .5em;
  }
  &__small{
    .swiper{
      height: calc(100% + 1.25em);
      &-slide{
        height: 20%;
        padding-bottom: 1.25em;
        &-thumb-active .gallery__img{
          &:before{
            opacity: 1;
          }
        }
      }
    }
    .gallery__img{
      cursor: pointer;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: 1px solid $primary;
        opacity: 0;
        transition: all .3s;
        border-radius: inherit;
        z-index: 2;
      }
    }
  }
}

.props{
  &__items{
    @include grid100gap(.75em);
  }
  &__link{
    font-size: .875em;
    line-height: 150%;
    font-weight: 700;
    color: $primary;
    &:hover{
      color: $red;
    }
  }
}

.prop-item{
  &__title{
    margin-right: 1em;
  }
}

.rating-block{
  display: flex;
  align-items: center;
}

.rating-block{
  &._disabled{
    pointer-events: none;
  }
}

.rating{
  cursor: pointer;
  &__stars{
    width: 6em;
    max-width: 100%;
    position: relative;
    img{
      width: 100%;
      display: block;
    }
  }
  &__fill{
    width: 0;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    overflow: hidden;
    img{
      width: 6em;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    &__grid{
      grid-template-columns: repeat(2, calc(50% - .75em));
    }
    @media screen and (min-width: 577px){
      &__col2{
        order: 3;
      }
      &__col3{
        grid-column: 2/3;
        grid-row: 1/3;
      }
    }
  }

  .gallery{
    grid-template-columns: 100%;
    gap: 1em;
    &__small,
    &__big{
      height: auto;
    }
    &__small{
      order: 2;
      .swiper-slide{
        width: 25%;
        padding: 0 1em 0 0;
      }
    }
    .swiper{
      height: auto;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item {
    &__grid {
      grid-template-columns: 100%;
    }
  }


}
