.slider{
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 1.5em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      padding-right: 1.5em;
      height: auto;
    }
    &-button{
      top: 50%;
      transform: translateY(-50%);
      margin: 0;
      &-prev{
        left: -1.5em;
      }
      &-next{
        right: -1.5em;
      }
      &.swiper-button-disabled{
        opacity: 0;
      }
    }
  }
  &__wrap{
    overflow: hidden;
  }
}

.swiper-button{
  width: 3em;
  height: 3em;
  box-shadow: 0 .25em 1em rgba(20, 20, 20, 0.10);
  background-color: $white;
  border-radius: 50%;
  svg{
    width: 1.5em;
  }
}

.product-slider{
  .swiper{
    &-slide{
      width: calc(100% / 6);
    }
    &-button{
      top: 9em;
      transform: unset;
    }
  }
  &._grid{
    @media screen and (min-width: 993px){
      .swiper{
        &-container{
          width: 100%;
        }
        &-wrapper{
          display: grid;
          grid-template-columns: repeat(6, calc(100% / 6 - 7.5em / 6));
          gap: 3em 1.5em;
          transform: unset !important;
        }
        &-slide{
          padding-right: 0;
          width: 100%;
        }
        &-nav{
          display: none;
        }
      }
    }
  }
}

.product-card,
.product-cart{
  &__price2{
    margin-left: .875em;
    position: relative;
    padding: 0 1px;
    &:before{
      content: '';
      @include centerAbsolute;
      width: 100%;
      height: .0625em;
      background-color: $red;
    }
  }
}

.product-card{
  height: 100%;
  display: flex;
  flex-direction: column;
  &__top{
    position: relative;
  }
  &__img{
    @include imgCover(100%);
    border-radius: .5em;
  }
  .favourite-btn{
    position: absolute;
    top: .625em;
    right: .625em;
  }
  .m-badge{
    position: absolute;
    left: 0;
    bottom: -.375em;
  }
  &__prices{
    display: flex;
    align-items: center;
    margin-top: 1em;
  }
  &__title{
    margin-top: .5em;
    @include truncate(2);
  }
  &__nav{
    margin-top: auto;
    padding-top: 1em;
  }
  &:not(._added){
    .cartcalc{
      display: none;
    }
  }
  &._added{
    .product-card__btn{
      display: none;
    }
  }
}

.animtocart {
  width: 10em;
  height: 10em;
  border-radius: .5em;
  @include center;
  transform: rotate(24deg);
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
    border-radius: inherit;
  }
}

.m-badge{
  padding: 0 .5em;
  height: 1.4375em;
  display: flex;
  align-items: center;
  border-radius: .5em;
  background-color: $red;
  color: $white;
  font-weight: 700;
}


@media screen and (max-width: 992px){
  .collection{
    overflow: hidden;
  }

  .product-slider{
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-slide{
        width: 15em;
      }
      &-nav{
        display: none;
      }
    }
  }
}


