.filter-slider{
  @include lastM(0 0 .75em);
  &__item{
    min-height: .9375em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: $line;
    border: none;
    box-shadow: none;
    height: .125em;
    border-radius: 0;
    .noUi-connect{
      background-color: $primary;
    }
    .noUi-handle{
      box-shadow: none;
      border: none;
      background-color: $primary;
      width: .875em;
      height: .875em;
      top: -.375em !important;
      right: -.375em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__inputs{
    margin-bottom: .75em;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: .75em;
    &-currency,
    &-title{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }
    &-title{
      left: .75em;
    }
    &-currency{
      right: .75em;
    }
  }
  .fg{
    position: relative;
    input{
      height: 3.14em;
      border: $border;
      background-color: transparent;
      padding: 0 2em 0 2.35em;
    }
    span{
      position: absolute;
      top: 50%;
      left: 1em;
      transform: translateY(-50%);
      font-size: .9375em;
      line-height: 120%;
      color: #A7AAAE;
    }
  }
}
