.referral,
.bonus{
  padding: 3.18em;
  border-radius: 1.25em;
  background: #F5F6F8;
}

.bonus{
  position: relative;
  &__img{
    position: absolute;
    width: 16.35em;
    top: -3.75em;
    right: -5em;
    pointer-events: none;
  }
  &__top{
    display: flex;
    align-items: flex-end;
    color: $red;
    font-weight: 700;
    &-num{
      font-size: 4.4375em;
    }
    &-title{
      font-size: 2.8125em;
      padding: 0 0 .2em .2em;
    }
  }
  &__items{
    margin-top: 1.75em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
  }
}

.bonus-item{
  padding-left: 2.19em;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    width: 0.625em;
    height: 0.625em;
    border-radius: 50%;
    background-color: $primary;
    top: .9em;
    left: .41em;
  }
  &__title{
    font-size: 1.5625em;
    line-height: 143%;
    font-weight: 700;
  }
  &__desc{
    font-size: .875em;
    line-height: 143%;
  }
}

.referral{
  margin-top: .625em;
  &__title{
    font-size: 1em;
    line-height: 143%;
    font-weight: 700;
  }
  &__desc{
    font-size: .75em;
    line-height: 143%;
  }
  &__bottom{
    margin-top: 1.1em;
    display: flex;
  }
  &__link{
    flex-grow: 1;
    padding: 0 1.12em;
    border-radius: 0.625em;
    background: $white;
    box-shadow: 0px 0px 16.2px 1.8px rgba(0, 0, 0, 0.11);
    display: flex;
    align-items: center;
    height: 2.625em;
    span{
      overflow: hidden;
    }
  }
  &__btn{
    padding: 0 1em;
    height: 2.625em;
    font-weight: 400;
    margin-left: 1.875em;
  }
}

.cabinet-status{
  padding: 3.18em 2.25em;
  border-radius: 1.25em;
  background: #F5F6F8;
  min-height: 24em;
  &__top{
    margin-bottom: 3.31em;
    min-height: 5.375em;
  }
  &__title{
    font-size: 1.5em;
    font-weight: 700;
  }
  &__info{
    padding: 1.25em;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 11.5em;
    background: #D9D9D9;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    margin: 0 auto 1.25em;
    border-radius: 1.25em;
    color: $white;
    &-icon{
      position: relative;
      &:before{
        content: '';
        @include centerAbsolute;
        width: 4.75em;
        height: 4.75em;
        border-radius: 50%;
        background: #FFC400;
        filter: blur(16.5px);
        margin-top: -17%;
      }
      &._green{
        &:before{
          background: #75FF7E;
        }
      }
    }
    img{
      height: 4.75em;
      width: auto;
      margin-bottom: 1.75em;
      position: relative;
      z-index: 3;
    }
  }
  &__desc{
    color: #777;
    font-size: 1.25em;
    margin-top: 1em;
  }
  &__hint{
    text-align: center;
    margin: 0 auto;
    width: 26em;
    max-width: 100%;
    color: #777777;
    .text20{
      line-height: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .bonus{
    padding: 1.6em;
    &__img{
      right: 0;
      width: 10em;
    }
  }

  .referral{
    padding: 1.6em;
  }

  .cabinet-status{
    padding: 1.6em;
    min-height: unset;
  }
}


@media screen and (max-width: 576px){
  .bonus{
    padding: 4em 1em 1em;
    border-radius: 1em;
    &__img{
      top: -2em;
    }
    &__items{
      grid-template-columns: 100%;
      margin-top: 1em;
    }
    &__top{
      font-size: .75em;
    }
  }

  .bonus-item{
    font-size: .875em;
  }

  .referral{
    border-radius: 1em;
    padding: 1em;
    &__bottom{
      display: block;
      margin-top: .75em;
    }
    &__btn{
      width: 100%;
      margin: .5em 0 0;
    }
  }

  .cabinet-status{
    border-radius: 1em;
    padding: 1em;
    &__top{
      font-size: .875em;
    }
    &__desc{
      font-size: 1em;
    }
  }
}
