.guide{
  &__grid{
    display: grid;
    grid-template-columns: 24.125em calc(100% - 26.5em);
    gap: 2.375em;
  }
  &__mob{
    display: none;
  }
  &__title{
    font-size: 3em;
    color: #6C6C6C;
    line-height: 130%;
    font-weight: 600;
  }
  .text{
    font-size: 1.25em;
    margin-top: 2em;
  }
}

.guide-nav{
  padding: 2.875em 0;
  background-color: #F5F5F5;
  border-radius: 1.25em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  @media screen and (min-width: 577px){
    display: flex !important;
  }
  &__show,
  &__link{
    padding: 0 2.625em;
    span{
      font-size: 1.5em;
      line-height: 130%;
      font-weight: 600;
      color: #6C6C6C;
    }
  }
  &__link{
    margin-bottom: 1.5em;
  }
  &__item{
    width: 100%;
    &._open{
      .guide-nav__show{
        img{
          transform: rotate(180deg);
        }
      }
    }
  }
  &__show{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    img{
      width: 1.1875em;
      transition: all .3s;
    }
  }
  &__hidden{
    padding-top: .5em;
    display: none;
  }
  &__sublink{
    display: flex;
    align-items: center;
    min-height: 3em;
    padding: .125em 2.625em .125em 4.25em;
    font-size: 1em;
    line-height: 130%;
    &._active{
      background-color: #E6EBF0;
    }
  }
}


@media screen and (max-width: 992px){
  .guide{
    &__grid{
      grid-template-columns: 18em calc(100% - 19.5em);
      gap: 1em 1.5em;
    }
    &__title{
      font-size: 1.75em;
    }
    .text{
      font-size: 1em;
    }
  }

  .guide-nav{
    padding: 2em 0;
    &__show,
    &__link{
      padding: 0 1.5em;
      span{
        font-size: 1.25em;
      }
    }
    &__link{
      margin-bottom: 1.25em;
    }
    &__sublink{
      padding: .125em 1.5em .125em 2.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .guide{
    &__grid{
      grid-template-columns: 100%;
    }
    &__mob {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-weight: 600;
      color: #6C6C6C;
      img{
        width: 1.1875em;
      }
      &._open {
        img {
          transform: rotate(180deg);
        }
      }
    }
  }

  .guide-nav{
    border-radius: 1em;
    margin-top: 1em;
    font-size: .75em;
    display: none;
    &__link{
      display: block;
    }
  }
}