.contacts{
  padding-top: 1.5em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 23.875em) 22.375em;
    gap: 1.5em;
  }
  &__col{
    padding: 2.5em 2.5em 5.5em;
    border-radius: 1.25em;
    background: #F8F8FA;
  }
  &__col2{
    padding: 2em;
    border-radius: 1.25em;
    background: #F8F8FA;
  }
  &__logo{
    width: 6.5em;
    display: block;
    margin-bottom: 1.4em;
  }
  &__title{
    font-size: 2.25em;
  }
  &__items{
    display: grid;
    grid-template-columns: 20em 20.75em auto;
    gap: 1.5em 2.5em;
    margin-top: 5.25em;
  }
  &__img{
    width: 9.25em;
    display: block;
    margin-bottom: 1.375em;
  }
  &__desc{
    line-height: 100%;
  }
  .socials{
    margin-top: 2.5em;
  }
}

.contacts-item{
  &__title{
    min-height: 3.75em;
  }
  &__email{
    margin-top: .375em;
  }
  &__time-title,
  &__link{
    font-size: 1.5em;
    line-height: 119%;
  }
  &__phones{
    margin-top: .29em;
    @include grid100gap(.6em);
  }
  &__time{
    margin-top: .375em;
    &-desc{
      font-size: 1.25em;
      line-height: 95%;
    }
  }
}

.contacts-form{
  padding: 1.125em 6.5em 1.125em 2.5em;
  border-radius: 0.625em;
  background: #F8F8FA;
  margin-top: 3.875em;
  &__title{
    font-size: 2.25em;
    font-weight: 600;
    max-width: 21em;
  }
  &__grid{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: 48.75em 22.5em;
    justify-content: space-between;
  }
  &__img{
    width: 100%;
    display: block;
  }
  .m-select{
    width: calc(50% - .75em);
  }
  &__fields{
    margin-top: 1.25em;
    row-gap: 1.25em;
  }
  .fg{
    textarea,
    input{
      font-size: 1.0625em;
      height: 3.53em;
      padding: 0 1.23em;
      background-color: $white;
      border-color: $white;
      &:focus {
        border-color: $primary;
      }
    }
    textarea{
      height: 8.5em;
    }
  }
  &__btn{
    margin-top: 1.25em;
  }
  &__hint{
    margin-top: 1em;
  }
}

._full{
  grid-column: 1/-1;
}


@media screen and (max-width: 992px){
  .contacts{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .75em));
    }
    &__col,
    &__col2{
      padding: 1.5em 1em;
    }
    &__title{
      font-size: 1.75em;
    }
    &__items{
      margin-top: 2em;
      grid-template-columns: 100%;
    }
    &__img{
      width: 7em;
      margin-bottom: 1em;
    }
  }

  .contacts-item{
    &__title{
      min-height: unset;
    }
  }

  .contacts-form{
    padding: 2em 1em;
    margin-top: 2.5em;
    &__title{
      font-size: 1.75em;
    }
    &__grid{
      grid-template-columns: 100%;
    }
    &__img{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__grid{
      grid-template-columns: 100%;
    }
    &__logo{
      width: 5em;
      margin-bottom: 1em;
    }
    .socials{
      margin-top: 1.5em;
    }
  }

  .contacts-item{
    &__title{
      font-size: .875em;
    }
    &__time-title,
    &__link{
      font-size: 1.25em;
    }
    &__time{
      &-desc{
        font-size: 1em;
      }
    }
  }

  .contacts-form{
    .m-select{
      width: 100%;
    }
    .fg{
      font-size: .875em;
    }
    &__fields{
      margin-top: .75em;
      grid-template-columns: 100%;
      gap: .75em;
    }
    &__btn{
      margin-top: 1em;
    }
  }
}
