.footer{
  margin-top: 2em;
  background-color: $bg;
}

.footer-top{
  padding: 3em 0;
}

.footer-nav{
  margin-top: .75em;
  color: $gray;
  @media screen and (min-width: 577px){
    display: block !important;
  }
  ul{
    @include grid100gap(.625em);
  }
  a{
    font-size: 1em;
    line-height: 150%;
  }
}

.footer-bottom{
  border-top: $border;
  color: $gray;
  padding: 1em 0;
}


@media screen and (max-width: 992px){
  .footer{
    margin-top: 1.25em;
  }

  .footer-top{
    padding: 1.5em 0;
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__c{
      margin-bottom: .75em;
    }
    .socials{
      justify-content: center;
    }
  }

  .footer-bottom{
    &__inner{
      flex-direction: column;
      align-items: unset;
    }
  }

  .footer-nav{
    display: none;
    &__show{
      display: flex;
      align-items: center;
      justify-content: space-between;
      &:after{
        content: '';
        width: 1em;
        height: 1em;
        background-image: url(../img/arrow-bottom2.svg);
        @include bgImgSize(100%);
        transition: all .3s;
      }
      &._open{
        &:after{
          transform: rotate(-180deg);
        }
      }
    }
  }

  .footer-contacts{
    text-align: center;
    margin-top: 1em;
    &__nav{
      justify-content: center;
    }
    ._mt24{
      margin-top: 1em;
    }
  }
}
