.product-cart{
  position: sticky;
  top: 6.5em;
  border-radius: 1.5em;
  padding: 1.5em;
  background-color: $bg;
  .m-badge{
    margin-left: auto;
  }
  &__title{
    margin-bottom: .5em;
  }
  &__available{
    margin-top: .5em;
    color: #38B241;
    &:before{
      content: '';
      flex-shrink: 0;
      width: .25em;
      height: .25em;
      border-radius: 50%;
      background-color: #38B241;
      margin-right: .5em;
    }
  }
  &__btn{
    width: 100%;
    margin-top: 1em;
  }
  &__marketplace{
    @include center;
    margin-top: 1em;
    &-item{
      height: 3.625em;
      margin: 0 1.375em;
      img{
        height: 100%;
        display: block;
      }
    }
  }
}

.product-detail{
  &__tabs{
    display: flex;
    border-bottom: $border;
  }
  .tab-content{
    margin-top: 2.5em;
  }
  &__desc{
    max-width: 58.5em;
  }
  &__props{
    row-gap: .75em;
  }
}

.reviews{
  padding-bottom: 2em;
  &__items{
    margin-top: 1em;
    @include grid100gap(1em);
  }
}

.reviews-info{
  padding: 3em;
  border-radius: 1.5em;
  background-color: $bg;
  .rating{
    &__stars{
      width: 8.5em;
    }
    &__fill{
      img{
        width: 8.5em;
      }
    }
  }
  &__sort{
    &-items{
      margin-left: 1em;
    }
    &-item{
      margin-right: 1em;
      color: $black;
      &:last-child{
        margin-right: 0;
      }
      &:hover{
        opacity: .9;
      }
      img{
        width: 1.25em;
        margin-right: .75em;
      }
    }
  }
  &__btn{
    margin-left: 5em;
    flex-shrink: 0;
  }
}

.review-stats{
  @include grid100gap(.66em);
  width: 24em;
  max-width: 100%;
  &__item{
    display: grid;
    grid-template-columns: .625em 1.25em calc(100% - 4.875em - 2.25em) 3em;
    align-items: center;
    gap: .75em;
    &-fill{
      border-radius: 1.25em;
      background-color: $line;
      height: .625em;
      overflow: hidden;
      span{
        background-color: $primary;
        border-radius: inherit;
        height: 100%;
        display: block;
        max-width: 100%;
      }
    }
  }
}

.reviews-card{
  padding: 3em;
  border-radius: 1.5em;
  border: $border;
  @include grid100gap(1.5em);
  .avatar{
    margin-right: 1em;
  }
  .rating-block{
    margin: 0 1em;
  }
  &__info{
    @include grid100gap(.5em);
  }
}

.avatar{
  width: 2.5em;
  height: 2.5em;
  flex-shrink: 0;
  border-radius: 50%;
  img{
    width: 100%;
    height: 100%;
    display: block;
    border-radius: inherit;
    object-fit: cover;
  }
}


@media screen and (max-width: 992px){
  .product-cart{
    padding: 1em;
    border-radius: 1em;
  }

  .reviews{
    padding-bottom: 1.25em;
  }

  .reviews-info{
    padding: 1em;
    border-radius: 1em;
    display: block;
    &__col2{
      display: block;
      margin-top: 1.5em;
    }
    &__btn{
      margin: 1em 0 0;
    }
  }

  .review-stats{
    width: 100%;
  }

  .reviews-card{
    padding: 1em;
    border-radius: 1em;
  }
}


@media screen and (max-width: 576px){
  .reviews-info{
    &__sort{
      display: block;
      &-items{
        margin: .75em 0 0;
      }
    }
  }

  .reviews-card{
    gap: 1em;
    &__top{
      flex-wrap: wrap;
    }
    &__date{
      margin-top: 1em;
    }
    &__info{
      font-size: .875em;
    }
  }
}
