.text{
  line-height: 150%;
  h2{
    margin-bottom: .8em;
  }
  p{
    @include lastM(0 0 1em);
  }
  hr{
    display: block;
    height: 2em;
    opacity: 0;
    margin: 0;
  }
  ol,ul{
    padding-left: .9em;
    @include grid100gap(1em);
    @include lastM(0 0 1.5em);
    li{
      position: relative;
      padding-left: 1em;
    }
  }
  ol {
    counter-reset: list;
    li {
      counter-increment: list;
      &:before {
        content: counter(list) '. ';
      }
    }
  }
  ul{
    li{
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
}



