.catalog{
  padding-top: 1.5em;
  &__grid{
    display: grid;
    grid-template-columns: 21em calc(100% - 22.5em);
    gap: 1.5em;
  }
  &__filter-btn{
    display: none;
  }
}

.filter{
  &__bg,
  &__top{
    display: none;
  }
  &__groups{
    @include grid100gap(.5em);
  }
  &__bottom{
    margin-top: 1.5em;
    @include grid100gap(.75em);
  }
}

.filter-group{
  padding: 1em 0;
  &:first-child{
    padding-top: 0;
  }
  &:last-child{
    padding-bottom: 0;
  }
  &--toggle{
    transition: all .3s;
    padding: 0;
    .filter-group__top{
      display: flex;
      align-items: center;
      max-width: 16.25em;
      cursor: pointer;
      &:after{
        content: '';
        margin-left: auto;
        background-image: url(../img/arrow-bottom.svg);
        @include bgImgSize(100%);
        width: .75em;
        height: .75em;
        flex-shrink: 0;
        transition: all .3s;
      }
    }
    .filter-group__bottom{
      display: none;
    }
  }
  &._active{
    padding: 1.5em 0;
    .filter-group__top:after{
      transform: rotate(-180deg);
    }
  }
  &__bottom{
    margin-top: .75em;
  }
  &__checks{
    @include grid100gap(1em)
  }
  &__categories{
    @include grid100gap(.25em);
    margin-left: -1em;
    max-width: 18em;
  }
  &__nav{
    margin-top: .75em;
  }
  &__more{
    ._view2{
      display: none;
    }
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    margin-bottom: .57em;
    font-size: .875em;
    line-height: 140%;
    &:last-child{
      margin-bottom: 0;
    }
    span{
      color: $gray;
    }
    &._active{
      color: $primary;
    }
  }
  &._show-all-checks{
    .filter-group__more{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
  &:not(._show-all-checks){
    .m-check:nth-child(5)~.m-check{
      //display: none;
    }
  }
}

.catalog-items{
  row-gap: 2.5em;
}

.page-nav{
  margin-top: 2.5em;
  @include grid100gap(2.5em);
}

.sort-select{
  margin-left: auto;
  .nice-select{
    border: none;
    padding: 0 0 0 1.75em;
    height: unset;
    width: auto;
    font-size: 1em;
    background-image: url(../img/filter.svg) !important;
    background-size: 1.125em auto !important;
    background-repeat: no-repeat !important;
    background-position: left center !important;
  }
  .current{
    font-size: .875em;
    line-height: 140%;
    color: $black;
  }
  .list{
    border-radius: 1em;
    margin-top: .1875em;
    width: auto;
    left: unset;
    right: 0;
    box-shadow: $shadow;
    border: none;
    padding: 1.5em;
    @include grid100gap(.75em);
  }
  .option{
    border: none;
    font-size: .875em;
    line-height: 143%;
    color: #344054;
    padding: 0;
    &:hover,
    &.focus,
    &.selected.focus{
      background-color: transparent;
      border-color: transparent;
      font-weight: 400;
      color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog{
    &__grid{
      grid-template-columns: 100%;
    }
    &__filter-btn{
      display: flex;
      width: 100%;
    }
    &__col{
      position: relative;
      z-index: 2;
    }
  }

  ._open-filter{
    .filter{
      transform: translateX(0);
      &__bg{
        display: block;
      }
    }
    .catalog{
      z-index: 10;
      position: relative;
      &__grid{
        z-index: 10;
        position: relative;
      }
    }
  }

  .filter{
    transition: all .5s;
    transform: translateX(-110%);
    z-index: 20;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 22.5em;
    height: $vh;
    background-color: $white;
    padding: 0 0 6.75em;
    &__top{
      display: flex;
      position: sticky;
      top: 0;
      z-index: 4;
      background-color: $white;
      padding: .5em .25em .5em 1em;
      border-bottom: $border;
    }
    &__show{
      position: fixed;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 3.25em;
      max-width: 22.5em;
      display: flex;
      border-radius: 0;
      z-index: 3;
    }
    &__groups{
      height: calc(100 * var(--vh) - 13em);
      overflow-y: auto;
      padding: 1em;
    }
    &__bottom{
      padding: 0 1em 1em;
      justify-content: center;
    }
  }

  .filter-group{
    &__categories{
      margin-left: -.5em;
      max-width: unset;
      width: calc(100% + 1em);
    }
    &--toggle{
      .filter-group__top{
        max-width: 100%;
        padding-right: .5em;
      }
    }
  }

  .page-nav{
    gap: 1.5em;
  }
}


@media screen and (max-width: 576px){
  .catalog-items{
    grid-template-columns: repeat(2,calc(50% - .5em));
    column-gap: 1em;
    .product-card__title{
      -webkit-line-clamp: 3;
    }
  }
}
